import Vue from 'vue'
import Router from 'vue-router'
import store from './store'

const routerList = [
  // 登入頁
  {
    path: '/login',
    name: 'login',
    component: () => import('@/components/login')
  },
  // 主頁
  {
    path: '/',
    name: 'todoList',
    component: () => import('@/components/todo-list')
  }, {
    path: '/todoEdit/:group/:id',
    name: 'todoEdit',
    component: () => import('@/components/todo-edit'),
    props: true
  }, {
    path: '/todoGroupList/:id',
    name: 'todoGroupList',
    component: () => import('@/components/todo-group-list'),
    props: true
  }, {
    path: '/nTodoGroupList/:group',
    name: 'nTodoGroupList',
    component: () => import('@/components/n-todo-group-list'),
    props: true
  }, {
    path: '/googleCalendar',
    name: 'googleCalendar',
    component: () => import('@/components/google-calendar')
  }, {
    path: '/starred',
    name: 'starred',
    component: () => import('@/components/starred')
  }, {
    path: '/',
    name: 'AppHeader',
    component: () => import('@/components/template/app-header')
  }, {
    path: '/',
    name: 'AppFooter',
    component: () => import('@/components/template/app-footer')
  }
]

Vue.use(Router)

const router = new Router({
  mode: 'history',
  linkActiveClass: 'active',
  routes: routerList
})

router.beforeEach((to, from, next) => {
  const user = localStorage.getItem('user')
  if (!user) {
    if (to.path === '/login') {
      next()
    } else {
      return next({ path: '/login' })
    }
  } else {
    if (to.path === '/login') {
      return next({ path: '/' })
    }
    if (to.name === 'nTodoGroupList' && from.name === 'todoEdit' && from.params.group === 'new') {
      store.commit('setEditTodoState', true)
    } else {
      store.commit('setEditTodoState', false)
    }
    next()
  }
})

export default router



