// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './service/router'

// UI
import 'bootstrap'
import 'bootstrap/dist/css/bootstrap.css'
import { library } from '@fortawesome/fontawesome-svg-core'
import { fas } from '@fortawesome/free-solid-svg-icons'
import { far } from '@fortawesome/free-regular-svg-icons'
import { fab } from '@fortawesome/free-brands-svg-icons'
import { FontAwesomeIcon, FontAwesomeLayers, FontAwesomeLayersText } from '@fortawesome/vue-fontawesome'
import 'animate.css/animate.min.css'

// plugin
import moment from 'moment'
import { HalfCircleSpinner } from 'epic-spinners'
import Swal from 'sweetalert2'
import Notifications from 'vue-notification'
import * as VueGoogleMaps from "vue2-google-maps";

// mixin
import IsLoadingMixin from './mixins/app-is-loading'

// service
import { rtdbPlugin } from 'vuefire'
import store from './service/store'

Vue.mixin(IsLoadingMixin)
Vue.prototype.$moment = moment
Vue.prototype.$bus = new Vue()
Vue.prototype.$messageHandle = (type, title, text) => {
  Vue.notify({
    group: 'msg',
    type: type, // warn, error, success
    title: title,
    text: text,
    duration: 1000
  })
}
Vue.prototype.$errorHandle = (title, text) => {
  Vue.notify({
    group: 'msg',
    type: 'error', // warn, error, success
    title: title,
    text: text,
    duration: 1500
  })
}

Vue.prototype.$swalSuccessHandle = (title, text) => {
  Swal.fire({
    title: title || '',
    text: text || '',
    type: 'success',
    timer: 2000,
    confirmButtonText: '確認'
  })
}
Vue.prototype.$swalErrorHandle = (title, text) => {
  Swal.fire({
    title: title || '',
    text: text || '',
    type: 'error',
    confirmButtonText: '確認'
  })
}
// 暫不使用
Vue.prototype.$swalDeleteHandle = (onConfirm) => {
  if(!onConfirm) {
    throw ('onConfirm must provided as a function')
  }
  Swal.fire({
    title: '確定刪除',
    text: '刪除後將不可修復',
    type: 'warning',
    showCancelButton: true,
    confirmButtonColor: '#3085d6',
    cancelButtonColor: '#d33',
    confirmButtonText: '確定',
    cancelButtonText: '取消'
  }).then((result) => {
    if (result.value) {
      Swal.fire({
        title: '刪除成功',
        type: 'success',
        timer: 2000,
        confirmButtonText: '確認'
      })
    }
  })
}
Vue.prototype.$swal = Swal;

Vue.prototype.$accountRegex = /^[a-zA-Z]+\d+/

Vue.use(rtdbPlugin)
Vue.use(Notifications)
Vue.use(VueGoogleMaps, {
  load: {
    key: "AIzaSyD0N6teAhJTbraJ-WHKeh2YKSS693TdOFU",
    libraries: "places" // necessary for places input
  }
});

library.add(fas, far, fab)
Vue.component('font-awesome-icon', FontAwesomeIcon)
Vue.component('font-awesome-layers', FontAwesomeLayers)
Vue.component('font-awesome-layers-text', FontAwesomeLayersText)
Vue.component('HalfCircleSpinner', HalfCircleSpinner)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  store,
  template: '<App/>'
})



