import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    user: null,
    userDBRef: '',
    loginType: null,
    groups: [],
    isAddTodo: false
  },
  mutations: {
    setUserInfo (state, payload) {
      console.log('[payload]', payload)
      if (state.loginType === 'app') {
        console.log('第三方登入, loginType =', state.loginType)
        state.userDBRef = ((payload['email']).split('@'))[0]
        window.localStorage.setItem('user', JSON.stringify(payload))
      } else {
        console.log('非 第三方登入, loginType =', state.loginType)
        state.userDBRef = payload.account
        // window.localStorage.setItem('user', payload)
        window.localStorage.setItem('user', JSON.stringify(payload))
      }
      state.user = payload
    },
    setLoginType (state, payload) {
      // console.log('[state.loginType]', state.loginType)
      state.loginType = payload
      window.localStorage.setItem('loginType', payload)
    },
    reset (state) {
      state.user = null
      localStorage.removeItem('user')
      localStorage.removeItem('loginType')
    },
    setGroups (state, payload) {
      state.groups = payload
    },
    setEditTodoState (state, payload) {
      state.isAddTodo = payload
    }
  },
  actions: {
    setUserInfo (context) {
      context.commit('setUserInfo')
    },
    setGroups (context) {
      context.commit('setGroups')
    }
  }
})

export default store



