<template>
  <nav class="navbar navbar-expand-lg navbar-light bg-light align-items-start">
    <router-link :to="{path:'/'}" class="navbar-brand font-weight-light logo" href="#">
      To Do List {{currentPath}}
      <div class="user text-primary">
        Hello, <span v-if="user">{{ user.email || user.account }}</span>
      </div>
    </router-link>
    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent"
      aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>

    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav ml-auto">
        <li class="nav-item">
          <router-link :to="{name: 'todoList'}" class="nav-link" href="#">
            <font-awesome-icon fixed-width class="ml-auto text-dark" icon="clipboard" />
            待辦清單
          </router-link>
        </li>
        <!-- <li class="nav-item">
          <router-link :to="{path:'/googleCalendar'}" class="nav-link" href="#">
            <font-awesome-icon fixed-width class="ml-auto text-dark" icon="calendar-alt" />
            Google日曆
          </router-link>
        </li> -->
        <li class="nav-item">
          <router-link :to="{path:'/starred'}" class="nav-link" href="#">
            <font-awesome-icon fixed-width class="ml-auto text-dark" icon="star" />
            已加星號
          </router-link>
        </li>
        <li class="nav-item">
          <a class="nav-link" href="#" @click="logout">
            <font-awesome-icon fixed-width class="ml-auto text-dark" icon="sign-out-alt" />
            登出
          </a>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>
import { rtdbDBAuth } from '@/service/rtdb_db'

export default {
  computed: {
    user () {
      return this.$store.state.user
    },
    loginType () {
      return this.$store.state.loginType
    },
    currentPath () {
      const path = this.$route
      return path.name === 'todoEdit' ? path.params.group ? path.params.group === 'new' ? ' - Add' : ' - Edit' : '' : ''
    }
  },
  methods: {
    logout () {
      if (this.loginType !== 'default') {
        rtdbDBAuth.signOut()
          .then(() => {
            this.isAuth = false
            this.$store.commit('reset')
            this.$router.push('/login')
          }).catch(err => console.error(err))
      } else {
        this.isAuth = false
        this.$store.commit('reset')
        this.$router.push('/login')
      }
    }
  }
}
</script>



