<template>
  <div id="app">
    <app-header class="fixed-top" v-if="!currentPathIsLogin" />
    <div v-if="!currentPathIsLogin" class="container-fluid p-3 content-wrap">
      <div class="row">
        <!-- <app-nav /> -->
        <div class="col-md-12 col-sm-12">
          <router-view />
        </div>
      </div>
    </div>
    <div v-else class="container-fluid p-3 login-wrap d-flex justify-content-center flex-column">
      <div class="row">
        <div  class="col-md-12 col-sm-12">
          <router-view />
        </div>
      </div>
    </div>

    <notifications group="msg" position="top center" class="mt-1" />
    <!-- <app-footer /> -->
  </div>
</template>

<script>
import AppHeader from '@/components/template/app-header'
import AppNav from '@/components/template/app-nav'
import AppFooter from '@/components/template/app-footer'
import { rtdbDB, getUserDBRef } from '@/service/rtdb_db'

export default {
  name: 'App',
  components: {
    AppHeader,
    AppNav,
    AppFooter
  },
  computed: {
    currentPathIsLogin () {
      return this.$route.path === '/login'
    },
    isLogin () {
      return this.$store.state.user !== null
    },
    getGroups () {
      return this.$store.state.groups
    }
  },
  methods: {
    getRandom (min, max) {
      return Math.floor(Math.random() * (max - min + 1)) + min
    }
  },
  mounted () {
    const loginType = window.localStorage.getItem('loginType')
    const userData = JSON.parse(window.localStorage.getItem('user'))
    if (userData) {
      this.$store.commit('setLoginType', loginType)
      this.$store.commit('setUserInfo', userData)
    }
    // random background img
    const bgID = this.getRandom(1, 5)
    $('head').append(`<style>.login-wrap:before{background: url('../static/login-bg-${bgID}.jpg') center center no-repeat;)} .login:before{background: url('../static/login-bg-${bgID}.jpg') center center no-repeat;)}</style>`)

    // get all groups, for group dropdown list use
    rtdbDB.ref(`${getUserDBRef()}/todoList`).on('value', snapshot => {
      const data = Object.keys(snapshot.val())
      this.$store.commit('setGroups', data)
    })
  }
}
</script>

<style lang='scss'>
@import "./assets/scss/app";
</style>



